import articleList from '@/components/articleList.vue';
import activityList from '@/components/activityList.vue';
export default {
	data() {
		return {
			id: -1,
			info: null,
			type: 0,
			attention: 0,
			tabs: [
				{
					name: '资讯',
					url: 'admin_user/getAdminUserNews',
					list: [],
					pageNum: 1,
					pageSize: 20,
					loading: true,
					more: false
				},
				{
					name: '活动',
					url: 'admin_user/getAdminActivity',
					list: [],
					pageNum: 1,
					pageSize: 20,
					loading: true,
					more: false
				}
			]
		};
	},
	components: { articleList, activityList },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		if (opt.id) {
			this.id = opt.id;
			this.attention = opt.attention - 0;
			this.$Fetch({
				url: 'admin_user/getDataById',
				data: { id: opt.id },
				success: res => {
					console.log(res)
					this.info = res;
				}
			});
			this.getList();
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		onChange(e) {
			this.type = e.detail.index;
			if (this.tabs[e.detail.index].list.length === 0) this.getList();
		},
		//关注
		doAttention() {
			this.$Fetch({
				url: 'Attention/add',
				data: { id: this.id },
				success: res => {
					this.attention = res.success;
					let str = res.success === 1 ? '已关注,砖瓦+' + res.point : '已取消关注';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		getList() {
			let tabs = this.tabs;
			let type = this.type;
			if (tabs[type].loading) {
				tabs[type].loading = false;
				this.tabs = tabs;
				this.$Fetch({
					url: tabs[type].url,
					data: {
						id: this.id,
						pageNum: tabs[type].pageNum,
						pageSize: tabs[type].pageSize
					},
					success: res => {
						let len = res.length;
						if (len > 0) {
							tabs[type].list = tabs[type].list.concat(res);
							if (len === tabs[type].pageSize) {
								tabs[type].pageNum++;
								tabs[type].loading = true;
								tabs[type].more = true;
							} else {
								tabs[type].more = false;
							}
						} else {
							tabs[type].more = false;
						}
						this.tabs = tabs;
					}
				});
			}
		}
	}
};
